<template>
	<view class="page">
		<view class="search-all-wrap">

			<view class="search-wrap">
				<text class="iconfont icon-faxianchaxun"></text>
				<input v-model="sdata" class="uni-input" @input="onInput" confirm-type="search" placeholder="搜索课程, 老师"
				 placeholder-style="color:#C7C7C7;" />
			</view>
		</view>
		<view class="flex align-center justify-center font-weight-bold course-tab">
			<view class="mx-5" @click="changeTab(index)" v-for="(item, index) in tabBars" :key="index" :class="tabIndex === index ? 'text-main font-lg' : 'font-md text-light-muted'">
				{{item.name}}
			</view>
		</view>

		<swiper :current="tabIndex" class="swiper-box" :style="{height:scrollH+'px'}" @change="onChangeTab">
			<!-- 全部 -->
			<swiper-item v-for="(index) in tabBars">
				<scroll-view scroll-y :style="'height:' + scrollH+'px;'" @scrolltolower="loadmoreEvent">

					<view @click="viewLiveInfo(item.id,item.sort)" class="live-list" v-for="(item, index) in list" :key="index">
						<view class="live-list-img-wrap">
							<image class="live-list-img" :src="item.thumb" mode="aspectFill"></image>
							<template v-if="item.sort == undefined">
								<text class="course-title-icon">套餐</text>
							</template>
							<template v-else-if="item.sort == 0">
								<text class="course-title-icon">内容</text>
							</template>
							<template v-else-if="item.sort == 1">
								<text class="course-title-icon">课程</text>
							</template>
							<template v-else>
								<text class="course-title-icon">直播</text>
							</template>
						</view>

						<view class="live-list-info">

							<view class="live-c-title">{{item.name}}</view>
							<template v-if="item.sort == 0">
								<view class="live-status living-status" v-if="item.islive == 1">
									{{item.lesson}}
								</view>
								<view class="live-status-tuwen" v-else>
									{{item.lesson}}
								</view>
							</template>
							<template v-else>
								<view class="live-status living-status" v-if="item.islive == 1">
									{{item.lesson}}
								</view>
								<view class="live-status" v-else>
									{{item.lesson}}
								</view>
							</template>
							<view class="live-teacher-price">
								<image class="live-teacher-avatar" :src="item.avatar" mode="aspectFill"></image>
								<text class="teacher-name">{{item.user_nickname}}</text>
								<view class="price-wrap">
									<text v-if="item.paytype == 0">免费</text>
									<text v-if="item.paytype == 2" class="pass">密码</text>
									<text v-if="item.paytype ==1" class="numPrice">
										{{'¥' + item.payval}}
									</text>
								</view>
							</view>
						</view>
					</view>

				</scroll-view>
			</swiper-item>
		</swiper>
		
		<template v-if="kongkong == true">
			<view :class="{xiangziwrap : (kongkong == true)}">
				<image class="xiangzi" src="../../static/images/xiangzi.png" mode="aspectFill"></image>
				<text class="xiangzi_txt">暂无相关课程</text>
			</view>
		</template>
	</view>
</template>

<script>
	import courseList from '@/components/common/course-list.vue'
	const app = getApp();

	export default {
		components: {

			courseList,
		},
		data() {
			return {
				scrollH: 500,
				tabIndex: 0,
				tabBars: [{
					name: "全部"
				}, {
					name: "未开始"
				}, {
					name: "学习中"
				}, {
					name: "已学完"
				}],
				list: [],
				loadmore: "上拉加载更多",
				hotCate: [],
				topicList: [],
				keyword: '',
				kongkong: false,
				sdata: ''
			}
		},
		onLoad() {
			if (app.globalData.userinfo == '') {
				uni.navigateTo({
					url: '../login/login'
				})
				return;
			}

			uni.getSystemInfo({
				success: (res) => {
					this.scrollH = res.windowHeight - res.statusBarHeight - 44;
				}
			});
			this.GetMyCourse(this.tabIndex, this.keyword);
		},
		methods: {
			onInput(e){
				
				this.keyword = e.detail.value;
				// 搜索方法
				this.GetMyCourse(this.tabIndex, this.keyword);
				},
			GetMyCourse(index, keywords) {
				let gData = app.globalData;
				uni.request({
					url: gData.site_url + 'Course.GetMyCourse',
					method: 'GET',
					data: {
						'uid': gData.userinfo.id,
						'token': gData.userinfo.token,
						'type': index,
						'keyword': keywords,
						'p': 0
					},
					success: res => {
						if(res.data.data.code == 700){
							uni.navigateTo({
								url: '../login/login',
							});
							return;
						}
						if (res.data.data.code == 0) {
							var array = [];
							array = res.data.data.info;
							this.kongkong = false;
							this.list = JSON.parse(JSON.stringify(array));
							// console.log(res);
							if (this.list.length == 0) {
								this.kongkong = true;
							}
						} else {
							this.kongkong = true;
						}

					},
				});
			},
			//切换选项卡
			changeTab(index) {
				this.tabIndex = index;
				this.GetMyCourse(this.tabIndex, this.keyword);
			},
			//滑动
			onChangeTab(e) {
				//切换当前索引
				this.tabIndex = e.detail.current;
				this.GetMyCourse(this.tabIndex, this.keyword);
			},
			//顶踩操作
			doSupport(e) {

				//拿到当前对象
				let item = this.list[e.index];
				let msg = e.type === 'support' ? '顶' : '踩';

				//判断之前是否已经顶踩
				if (item.support.type === '') {
					item.support[e.type + '_count']++;
				} else if (item.support.type === 'support' && e.type === 'unsupport') {
					//顶 -1
					item.support.support_count--;
					//踩 +1
					item.support.unsupport_count++;
				} else if (item.support.type === 'unsupport' && e.type === 'support') {
					//之前踩了
					// 顶+1
					item.support.support_count++;
					// 踩-1
					item.support.unsupport_count--;
				}

				item.support.type = e.type;
				uni.showToast({
					title: msg + '成功'
				});
			},
			loadmoreEvent() {
				//验证当前是否处于可加载状态
				if (this.loadmore !== '上拉加载更多') return;
				//设置加载状态
				this.loadmore = '加载中...';
				//模拟请求数据
				setTimeout(() => {
					//加载数据
					this.list = [...this.list, ...this.list];
					//设置加载状态
					this.loadmore = '上拉加载更多';
				}, 2000)
			},
			openSearch() {
				uni.navigateTo({
					url: '../search/search?type=topic',

				});
			},
			viewLiveInfo(liveCourseId, sorttype) {
				if (getApp().globalData.userinfo == '') {
					uni.navigateTo({
						url: '../login/login'
					})
					return;
				}
				//套餐
				if (sorttype == undefined) {
					uni.navigateTo({
						url: '../taocaninfo/taocaninfo?courseid=' + liveCourseId
					});
				}
				//
				else if (sorttype == 0) {
					uni.navigateTo({
						url: '../content-info/content-info?courseid=' + liveCourseId
					});
				} else if (sorttype == 1) {
					uni.navigateTo({
						url: '../courseinfo/courseinfo?courseid=' + liveCourseId
					});
				} else
					uni.navigateTo({
						url: '../live_course_info/live_course_info?courseid=' + liveCourseId
					});
			},
		}
	}
</script>

<style>
	@import url("/static/css/course_list.css");
.page{
	width: 100%;
	height: 100%;
}
	.swiper-box {
		margin-top: 20rpx;
	}

	.course-tab {
		margin-top: 20rpx;
	}

	.s-all-wrap {
		position: relative;
	}

	.search-all-wrap {
		/* #ifdef MP-WEIXIN */
		margin-top: 150rpx;
		/* #endif */
		/* #ifndef MP-WEIXIN */
		margin-top: 80rpx;
		/* #endif */
		
		/* #ifdef H5 */
			margin-top: 20rpx;
		/* #endif */
		width: 100%;
		height: 65rpx;
		line-height: 65rpx;
	}
	.search-wrap {
		width: 80%;
		height: 100%;
		line-height: 65rpx;
		border-radius: 30rpx;
		margin-left: 20rpx;
		padding-left: 20rpx;
		background-color: #F5F5F5;
		float: left;
	}

	.search-wrap text,
	.search-wrap input {
		float: left;
		background-color: #F5F5F5;
		height: 100%;
		padding: 0;
	}

	.search-wrap text {
		margin-right: 20rpx;
		color: #C7C7C7;
	}

	.search-wrap input {
		width: 70%;
	}

	.reset-search {
		width: 10%;
		margin-left: 20rpx;
		float: left;
		color: #64D3AD;
	}

	
	.showsearch {
		display: block;
	}

	.search-title-wrap {
		width: 95%;
		margin: 0 auto;
		padding-left: 5%;
		height: 80rpx;
	}

	.search-title-wrap .s-teacher-title,
	.search-title-wrap .s-course-title {
		font-weight: bold;
		font-size: 28rpx;
	}

	.search-wrap {
		position: absolute;
		width: 90%;
		height: 70rpx;
		line-height: 65rpx;
		border-radius: 30rpx;
		background-color: #F5F5F5;
		margin-left: 30rpx;
	}


	/* 大班课单独样式 */
	.liveinfo-wrap {
		padding-top: 2rpx;
		min-height: 1500rpx;
		background-color: #FFFFFF;
	}

	.live-list {
		width: 90%;
		height: 190rpx;
		margin: 10rpx auto;
		border-radius: 8rpx;
		background-color: #FFFFFF;
	}

	/* .live-c-title {
		line-height: 35rpx;
		height: 40%;
	} */

	/* .live-teacher-price {
		margin-top: 10rpx;
	} */

	/* .price-wrap {
		margin-left: 55% !important;
	} */

	.xiangziwrap {		
		position: absolute;
		left: calc(50% - 50px);
		top: calc(50% - 50px);
		width: 100px;
		height: 100px;
 	}

	.xiangzi {
		width: 100%;
		height: 100%;
	}

	.xiangzi_txt {
		width: 100%;
		display: block;
		text-align: center;
		font-size: 26rpx;
		color: #C7C7C7;
	}
</style>
